import { Heading1, Heading2 } from '@components/ui/Headers';
import useMatchesData, { useRootLayoutData } from '@lib/hooks/useMatchesData';
import { OrgCombobox } from 'app/components/OrgCombobox';
import { forwardRef } from 'react';

type PageHeaderProps = {
  pageTitle: string;
  showOrgCombo?: boolean;
  org?: { id: number; uuid: string; name: string };
  navigateTo?: 'inbound' | 'inprocess' | 'completed' | 'outbound' | 'supervisors';
} & React.HTMLAttributes<HTMLDivElement>;

const PageHeaderRef = forwardRef<HTMLDivElement, PageHeaderProps>(
  function PageHeader(
    {
      org,
      pageTitle,
      navigateTo,
      showOrgCombo = true,
      ...props
    }: PageHeaderProps,
    ref: React.Ref<HTMLDivElement>
  ) {
    const { parentNode, orgFlatArray } = useRootLayoutData();

    return (
      <div
        {...props}
        ref={ref}
        className={`flex justify-between mb-5${props.className ? ` ${props.className}` : ''}`}>
        <div className="flex flex-col">
          {org && (
            <>
              <h1 className="text-2xl text-gray-900 mt-2.5">{org.name}</h1>
              <Heading2>{pageTitle}</Heading2>
            </>
          )}
          {!org && (
            <h1 className="text-2xl text-gray-900">{pageTitle}</h1>
          )}
        </div>
        {showOrgCombo && org && (
          <OrgCombobox
            orgArray={orgFlatArray}
            parentNode={parentNode}
            navigateTo={navigateTo}
            selected={{ id: org.id, uuid: org.uuid, name: org.name }}/>
        )}
      </div>
    );
  }
);

export default PageHeaderRef;
